<template>
    <div style="padding: 10px;">
        <v-row :key="1">
            <v-col :key="1" style="text-align: center;">
                <div :style="{background: `${themePrimaryColor}`, width:'80%', 'margin-left': '10%'}">
                    <v-img ref="imgPreview" max-height="67vh" aspect-ratio="1.7778" contain src="@/assets/themepreview.png"></v-img>
                </div>
            </v-col>
        </v-row>
        <v-row :key="2">
            <v-col :key="1">
                <v-color-picker v-model="themePrimaryColor"
                                show-swatches
                                hide-canvas
                                hide-inputs></v-color-picker>
            </v-col>
        </v-row>
        <v-row :key="3">
            <v-col :key="1" style="text-align: right;">
                <v-btn class="mr-4" @click.prevent="setDefaultDesktopColor">Default</v-btn>
                <v-btn class="mr-4" @click.prevent="changeThemePrimaryColor">Save</v-btn>
            </v-col>
        </v-row>
        <!--<v-row :key="4">
            <v-col :key="1">
                {{ themePrimaryColor }}
            </v-col>
        </v-row>-->
    </div>
</template>

<script>
    export default {
        data: () => ({
            themePrimaryColor: '',
        }),
        mounted() {
            if (localStorage.getItem("themePrimaryColor") === null) {
                this.setDefaultDesktopColor();
            }
            else {
                this.themePrimaryColor = localStorage.themePrimaryColor;
            }
        },
        methods: {
            changeThemePrimaryColor() {
                this.$store.dispatch('changeThemePrimaryColorAction', this.themePrimaryColor);
            },
            defaultThemePrimaryColor() {
                this.$store.dispatch('setDefaultThemePrimaryColorAction');
            },
            setDefaultDesktopColor() {
                this.defaultThemePrimaryColor();
                this.themePrimaryColor = this.$store.getters.themePrimaryColor;
            },

        },
        watch: {
            themePrimaryColor(newThemePrimaryColor) {
                localStorage.themePrimaryColor = newThemePrimaryColor;
            }
        }
    }
</script>

<style scoped>
    .v-color-picker {
        max-width: 100% !important;
    }
</style>