<template>
    <v-app>
        <!-- :style="[ isMobile  ? { background: 'linear-gradient(270deg, rgba(2,0,36,1) 0%, rgba(0,139,241,1) 35%, rgba(0,212,255,1) 100%)' } : { backgroundImage: `url(${desktopUrl})`}]"
                     class="desktop" -->
        <v-content>
            <!--<span>isMobile test: {{ this.test }}</span>-->
            <v-container :style="{ backgroundImage: `url(${desktopUrl})`}"
                         class="desktop"
                         fluid>
                <v-list dense two-line
                        style="width:110px;background-color: rgba(255, 255, 255, 0) !important;">
                    <v-list-item link v-on:click="toggleApp(11, true)">
                        <v-list-item-content style="text-align:center;">
                            <v-list-item-icon :style="[isMobile ? {'margin-top':'0!important','margin-bottom': '0!important','margin-left': '22px' } : { 'margin-bottom': '5px','margin-left': '18px' }]">
                                <v-icon :size="isMobile ? 26 : 40" color="#1565C0">mdi-file-document</v-icon>
                            </v-list-item-icon>
                            <v-list-item-title :style="[isMobile ? { 'font-size': '.7rem' } : { 'font-size': '.8125rem' }]">Doc</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item link v-on:click="toggleApp(10, true)">
                        <v-list-item-content style="text-align:center;">
                            <v-list-item-icon :style="[isMobile ? {'margin-top':'0!important','margin-bottom': '0!important','margin-left': '16px' } : { 'margin-bottom': '5px','margin-left': '14px' }]">
                                <v-icon v-if="isMobile" size="26" style="margin-left: 6px;" color="#3cb371">mdi-file-excel-outline</v-icon>
                                <ExcelIcon v-if="!isMobile" />
                            </v-list-item-icon>
                            <v-list-item-title :style="[isMobile ? { 'font-size': '.7rem' } : { 'font-size': '.8125rem' }]">Spreadsheet</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item link v-on:click="toggleApp(21, true)">
                        <v-list-item-content style="text-align:center;">
                            <v-list-item-icon :style="[ isMobile ? {'margin-top':'0!important','margin-bottom': '0!important','margin-left': '22px' } : { 'margin-bottom': '5px','margin-left': '18px' }]">
                                <v-icon :size="(isMobile) ? 26 : 40" color="#9400d3">mdi-image</v-icon>
                            </v-list-item-icon>
                            <v-list-item-title :style="[isMobile ? { 'font-size': '.7rem' } : { 'font-size': '.8125rem' }]">Photopea</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item v-for="app in installedApps"
                                 link
                                 v-bind:key="app.id"
                                 v-on:click="toggleAppUrl(24, app.url, true)">
                        <v-list-item-content style="text-align:center;">
                            <v-list-item-icon :style="[ isMobile ? {'margin-top':'0!important','margin-bottom': '0!important','margin-left': '22px' } : { 'margin-bottom': '5px','margin-left': '18px' }]">
                                <v-icon :size="(isMobile) ? 26 : 40" :color="randomColor()">mdi-application</v-icon>
                            </v-list-item-icon>
                            <v-list-item-title :style="[isMobile ? { 'font-size': '.7rem' } : { 'font-size': '.8125rem' }]">{{ app.name }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-container>
        </v-content>

        <!--<div class="h-100" :style="{ backgroundImage: `url(${desktopUrl})` }">

    </div>-->
        <v-navigation-drawer v-if="!(isMobile || hideSideBar)" :color="themePrimaryColor" v-model="drawer" absolute right width="307" style="z-index:28">
            <v-layout justify-space-between column fill-height>
                <v-list three-line dense>
                    <v-list-item>
                        <v-list-item-content>
                            <v-list-item-title>Welcome to an alpha version of SilveOS.com.</v-list-item-title>
                            <v-list-item-subtitle>It is a virtual desktop environment embedded in a web browser.</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
                <v-spacer></v-spacer>
                <v-list subheader dense>
                    <v-subheader>Ads</v-subheader>
                    <v-list-item-group color="primary">
                        <v-list-item @click="openAdsterraDirect">
                            <v-list-item-content>
                                <v-list-item-title>Adsterra Direct link</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                        <v-list-item @click="openPropellerAdsDirect">
                            <v-list-item-content>
                                <v-list-item-title>PropellerAds Direct link</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                    </v-list-item-group>
                </v-list>
                <v-list subheader two-line dense justify-end class="mb-12">
                    <v-subheader>Donate to The Project</v-subheader>
                    <v-list-item selectable>
                        <v-list-item-content>
                            <v-list-item-title>Bitcoin address:<v-btn style="margin-left: 132px !important;" x-small @click="copyBtcAddress">copy</v-btn></v-list-item-title>
                            <v-list-item-subtitle id="btcAddress">1C5uqnU4MHZMhDCQHxECQQuVpZs6LZooRK</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-layout>
        </v-navigation-drawer>

        <v-app-bar app :bottom="true"
                   :height="(isMobile) ? 32 : 48"
                   :color="themePrimaryColor"
                   style="z-index:29">
            <v-menu left
                    top
                    allow-overflow
                    offset-y>
                <template v-slot:activator="{ on }">
                    <v-btn icon v-on="on" @click="forceRerenderStartMenu">
                        <!--<v-icon>mdi-apps</v-icon>-->
                        <v-icon>mdi-view-grid</v-icon>
                    </v-btn>
                </template>

                <v-card :style="startMenuStyle" class="startcard" :color="themePrimaryColor">
                    <v-card-actions style="padding:0em;">
                        <StartMenu :key="startMenuKey" />
                    </v-card-actions>
                </v-card>

            </v-menu>
            <!--<v-app-bar-nav-icon @click.stop="start = !start"></v-app-bar-nav-icon>-->
            <div class="search" v-if="!isMobile">
                <v-text-field hide-details
                              v-model="search"
                              label="Type here to search"
                              prepend-inner-icon="mdi-magnify"
                              dark
                              single-line
                              v-on:keyup="searchInternet"
                              @click:prepend-inner="toggleAppUrl(2, 'https://www.google.com/search?igu=1&q=' + search, true)"></v-text-field>
            </div>
            <v-btn width="32" height="32" class="toolbar-button" tile icon v-if="isMobile" @click="toggleAppUrl(2, 'https://www.google.com/search?igu=1&q=', true)">
                <v-icon>mdi-magnify</v-icon>
            </v-btn>
            <v-tooltip top v-for="app in apps" :key="app.id">
                <template v-slot:activator="{ on }">
                    <v-btn :width="(isMobile) ? 32 : 48" :height="(isMobile) ? 32 : 48" :class="[app.isOpen ? 'toolbar-button-active' : 'toolbar-button']" tile icon v-show="app.isTaskbarVisible" @click="toggleAppState(app.id)" v-on="on">
                        <v-icon :size="(isMobile) ? 24 : 32" :color="app.iconcolor">{{app.icon}}</v-icon>
                    </v-btn>
                </template>
                <span>{{app.title}}</span>
            </v-tooltip>
            <v-spacer></v-spacer>
            <v-btn icon style="transform: rotate(270deg);">
                <v-icon :disabled="!onLine">{{ onLine ? 'mdi-signal-variant' : 'mdi-earth' }}</v-icon>
            </v-btn>
            <DateTime :isMobile="isMobile" />
            <v-btn v-if="!(isMobile || hideSideBar)" icon @click.stop="drawer = !drawer">
                <v-icon>mdi-message-outline</v-icon>
            </v-btn>
        </v-app-bar>

        <silveos-window-style-white v-for="app in apps" :key="app.id">
            <silveos-window :icon="app.icon"
                            :iconcolor="app.iconcolor"
                            :style="[isMaximizedWindow(app) ? { width: 'calc(100vw - (100vw - 100%))',  height: 'calc(100vh - ' + taskbarHeight + 'px)'} : { width: app.width,  height: app.height}]"
                            :taskbarHeight="taskbarHeight"
                            :titlebarPadding="isMobile ? '0px' : '8px'"
                            positionHint="center"
                            :title="app.title"
                            :minimizeButton="true"
                            :closeButton="true"
                            :maximizeButton="app.isResizible"
                            @closeButtonClick="closeApp(app.id)"
                            @minimizeButtonClick="toggleAppState(app.id)"
                            :isOpen.sync="app.isOpen"
                            :isMaximized.sync="app.isMaximized"
                            :draggable="true"
                            :resizable="app.isResizible">
                <template v-if="app.type === 0">
                    <div class="wrap" v-if="app.isTaskbarVisible">
                        <iframe class="frame"
                                v-bind:src="(isMobile && app.mobileUrl) ? app.mobileUrl : app.url"></iframe>
                    </div>
                </template>
                <template v-else-if="app.type === 1">
                    <component v-bind:is="app.component"></component>
                </template>
                <template v-else>
                    <component v-bind:is="app.component" v-bind:url="app.url"></component>
                </template>

            </silveos-window>
        </silveos-window-style-white>

        <v-overlay absolute
                   :opacity="shutdownOpacity"
                   :value="isShutdown"
                   :z-index="100">
            <div v-show="shutdownOpacity < 1">
                <v-progress-circular indeterminate
                                     color="white"></v-progress-circular><span style="margin-left:12px;font-size:x-large;">Shutting down</span>
            </div>
        </v-overlay>
    </v-app>
</template>

<script>
    import Vue from 'vue';
    //import Window from './components/Window.vue';
    import DateTime from './components/DateTime';
    import StartMenu from './components/StartMenu';
    import Calculator from './components/Calculator.vue';
    import Calendar from './components/Calendar.vue';
    import Settings from './components/Settings.vue';
    import IFrameBypass from './components/IFrameBypass.vue';
    import ExcelIcon from './assets/excel.svg';
    import * as  SWindow from "./components/Windows";

    Vue.use(SWindow);

    //import { globalMixin } from './components/mixins/Core.js'

    export default {
        name: 'App',
        //mixins: [globalMixin],
        components: {
            //Window,
            DateTime,
            StartMenu,
            Calculator,
            Calendar,
            Settings,
            IFrameBypass,
            ExcelIcon
        },
        data: () => ({
            drawer: false,
            //start: false,
            search: null,
            showSearch: false,
            showInternetBrowser: false,
            startMenuKey: 0,
            taskbarItemOrder: 1,
            onLine: navigator.onLine,
            //adContent: ''
        }),
        //this method is not executed for Android webview
        created() {
            //if (localStorage.themePrimaryColor) {
            //    this.$vuetify.theme.themes.dark.primary = localStorage.themePrimaryColor;
            //}
            //else
            //{
            //    this.$vuetify.theme.themes.dark.primary = this.themePrimaryColor;
            //}
            this.$vuetify.theme.dark = true;

            if (localStorage.getItem("desktopUrl") != null) {
                this.$store.dispatch('changeDesktopUrlAction', localStorage.desktopUrl);
            }

            if (localStorage.getItem("themePrimaryColor") != null) {
                this.$store.dispatch('changeThemePrimaryColorAction', localStorage.themePrimaryColor);
            }
            this.$store.dispatch('getInstalledApps');
            //if (this.isWindowSmall || this.isMobile) {
            //    //this.apps.forEach(app => app.id != 18 ? app.isMaximized = true : app.isMaximized = false);
            //    this.apps.forEach(app => {
            //        if (app.id != 18) {
            //            app.isMaximized = true;
            //            app.isResizible = false;
            //        } else {
            //            app.isMaximized = false;
            //            //app.isResizible = true;
            //        }
            //    });
            //}
        },
        methods: {
            // eslint-disable-next-line
            updateIsOpenWindow(isOpen) {
                // By emitting the 'update' event in every intermediary component we can pass data
                // from GrandchildComponent to ChildComponent and from there to the parent
                this.$emit('update:isOpen', false)
            },
            toggleAppState(id) {
                this.$store.dispatch('toggleAppStateAction', id)
            },
            toggleApp(id, isOpen) {
                this.$store.dispatch('toggleAppAction', { id, isOpen })
            },
            toggleAppUrl(id, url, isOpen) {
                this.$store.dispatch('toggleAppUrlAction', { id, url, isOpen })
            },
            closeApp(id) {
                this.$store.dispatch('closeAppAction', id)
            },
            searchInternet(e) {
                if (e.keyCode === 13) {
                    this.toggleAppUrl(2, 'https://www.google.com/search?igu=1&q=' + this.search, true);
                }
            },
            forceRerenderStartMenu() {
                this.startMenuKey += 1;
            },
            openAdsterraDirect() {		
                window.open("https://www.passeura.com/c55cjgm3u?key=fd5c8ef8270209f0dffbb7c5c8355385", "_blank");
            },
            openPropellerAdsDirect() {		
                window.open("http://serconmp.com/afu.php?zoneid=2964282", "_blank");
            },
            selectElementText(el){
                var range = document.createRange(); // create new range object
                range.selectNodeContents(el); // set range to encompass desired element text
                var selection = window.getSelection(); // get Selection object from currently user selected text
                selection.removeAllRanges(); // unselect any user selected text (if any)
                selection.addRange(range); // add range to Selection object to select it
            },
            copyBtcAddress () {
                //let btcAddress = this.$refs.btcAddress;
                var btcAddress = this.$el.querySelector('#btcAddress');
                this.selectElementText(btcAddress);
                //btcAddress.select();

                try {
                    document.execCommand('copy');
                    //var successful = document.execCommand('copy');
                    //var msg = successful ? 'successful' : 'unsuccessful';
                    //console.log('Copying text command was ' + msg);
                } catch (err) {
                    //console.log('Oops, unable to copy');
                }
            },
            updateOnlineStatus(e) {
                const { type } = e;
                this.onLine = type === 'online';
            },
            isMaximizedWindow(app) {
                //var res = (this.isWindowSmall || this.isMobile) ? true : false;
                if (this.isWindowSmall || this.isMobile) {
                    if (app.id != 18) {
                        app.isMaximized = true;
                        app.isResizible = false;
                        return true;
                    } else {
                        return false;
                    }
                } else {
                    return app.isMaximized;
                }
            },
            randomColor() {
                return '#' + (Math.random() * 0xFFFFFF << 0).toString(16);
            }
        },
        computed: {
            apps() {
                return this.$store.getters.apps
            },
            isShutdown() {
                return this.$store.getters.isShutdown
            },
            shutdownOpacity() {
                return this.$store.getters.shutdownOpacity
            },
            desktopUrl() {
                return this.$store.getters.desktopUrl
            },
            themePrimaryColor() {
                return this.$store.getters.themePrimaryColor
            },
            installedApps() {
                return this.$store.state.installedApps;
            },
            isWindowSmall() {
                return window.innerWidth <= 720 || window.innerHeight <= 375
            },
            isMobile() {
                return Boolean(this.$route.query && this.$route.query.mobile)
            },
            hideSideBar() {
                return false
            },
            taskbarHeight() {
                return this.isMobile ? 32 : 48
            },
            startMenuStyle() {
                return {
                    '--start-menu-height': this.isMobile ? '84vh' : '60vh',
                    '--start-menu-width': this.isMobile ? '64vw' : '46vw',
                }
            }
        },
        mounted() {
            //this.adContent = document.getElementById('divaddisplaynone').innerHTML;
            window.addEventListener('online', this.updateOnlineStatus);
            window.addEventListener('offline', this.updateOnlineStatus);
        },
        beforeDestroy() {
            window.removeEventListener('online', this.updateOnlineStatus);
            window.removeEventListener('offline', this.updateOnlineStatus);
        }
    };
</script>

<style>
    /*@-ms-viewport {
        width: device-width;
    }*/
    .startcard {
        height: var(--start-menu-height);
        width: var(--start-menu-width);
    }
    .v-menu__content{
        overflow-y: hidden!important;
    }
    .v-navigation-drawer__content {
        overflow-y: hidden !important;
    }
    /*@media screen and (max-width: 1500px) {
        .startcard {
            height: 60vh;
            width: 47vw;
        }
    }
    @media screen and (max-width: 1300px) {
        .startcard {
            height: 60vh;
            width: 49vw;
        }
    }
    @media screen and (max-width: 720px) {
        .startcard {
            height: 67vh;
            width: 54vw;
        }
    }*/
    .search {
        width: 20vw!important;
    }
    .desktop {
        height: 100% !important;
        background-repeat: no-repeat;
        background-size: 100% 100%;
    }
    .wrap {
      display: flex;
      flex-direction: column;
      min-height: 0;
      height: 100%;
      overflow:hidden;
    }

    .frame {
      flex: 1 1 auto;
      border: 0;
    }
    .toolbar-button {
        margin-left: 0.3vw;
    }
    .toolbar-button-active {
        margin-left: 0.3vw;
        border-bottom: 2px solid white;
    }
</style>