    mutations: {
        shutdown(state) {
            state.isShutdown = true;
        },
        shutdownEnd(state) {
            state.shutdownOpacity = 1;
        },
        toggleAppState(state, id) {
            id--;
            state.apps[id].isOpen = !state.apps[id].isOpen;
        },
        closeApp(state, id) {
            id--;
            state.apps[id].isOpen = false;
            state.apps[id].isTaskbarVisible = false;
        },
        toggleApp(state, { id, isOpen }) {
            id--;
            state.apps[id].isOpen = isOpen;
            state.apps[id].isTaskbarVisible = isOpen;
        },
        toggleAppUrl(state, { id, url, isOpen }) {
            id--;
            state.apps[id].url = url;
            state.apps[id].isOpen = isOpen;
            state.apps[id].isTaskbarVisible = isOpen;
        },
        changeDesktopUrl(state, desktopUrl) {
            state.desktopUrl = desktopUrl;
        },
        setDefaultDesktopUrl(state) {
            state.desktopUrl = desktopUrl;
        },
        changeThemePrimaryColor(state, themePrimaryColor) {
            state.themePrimaryColor = themePrimaryColor;
        },
        setDefaultThemePrimaryColor(state) {
            state.themePrimaryColor = defaultThemePrimaryColor;
        }
    },

    actions: {
        shutdownAction({ commit }) {
            commit('shutdown')
        },
        shutdownEndAction({ commit }) {
            commit('shutdownEnd')
        },
        toggleAppStateAction({ commit }, id) {
            commit('toggleAppState', id)
        },
        toggleAppAction({ commit }, { id, isOpen }) {
            commit('toggleApp', { id, isOpen } )
        },
        toggleAppUrlAction({ commit }, { id, url, isOpen }) {
            commit('toggleAppUrl', { id, url, isOpen })
        },
        closeAppAction({ commit }, id) {
            commit('closeApp', id)
        },
        changeDesktopUrlAction({ commit }, desktopUrl) {
            commit('changeDesktopUrl', desktopUrl)
        },
        setDefaultDesktopUrlAction({ commit }) {
            commit('setDefaultDesktopUrl')
        },
        changeThemePrimaryColorAction({ commit }, themePrimaryColor) {
            commit('changeThemePrimaryColor', themePrimaryColor)
        },
        setDefaultThemePrimaryColorAction({ commit }) {
            commit('setDefaultThemePrimaryColor')
        },
        async deleteInstalledApp(context, app) {
            // eslint-disable-next-line
            console.log('store is being asked to delete ' + app.id);
            await idb.deleteApp(app);
        },
        async getInstalledApps(context) {
            context.state.installedApps = [];
            let apps = await idb.getApps();
            apps.forEach(c => {
                context.state.installedApps.push(c);
            });
        },
        async saveInstalledApp(context, app) {
            await idb.saveApp(app);
        }
    },

    getters: {
        apps(state) {
            return state.apps
        },
        isShutdown(state) {
            return state.isShutdown
        },
        shutdownOpacity(state) {
            return state.shutdownOpacity
        },
        desktopUrl(state) {
            return state.desktopUrl
        },
        themePrimaryColor(state) {
            return state.themePrimaryColor
        },
    }
