<template>
    <div style="padding: 10px;">
        <v-row :key="1">
            <v-col :key="1">
                <!--<v-text-field v-model="desktopUrl"
                  :counter="100"
                  :rules="rules"
                  label="Url"
                  required
                  clearable></v-text-field>-->
                <v-autocomplete v-model="desktopUrl"
                                :disabled="isUpdating"
                                :items="wallpapers"
                                filled
                                color="blue-grey lighten-2"
                                label="Image url"
                                item-text="src.original"
                                item-value="src.original">
                    <template v-slot:item="data">
                        <template v-if="typeof data.item !== 'object'">
                            <v-list-item-content v-text="data.item"></v-list-item-content>
                        </template>
                        <template v-else>
                            <v-list-item-avatar>
                                <img :src="data.item.src.tiny">
                            </v-list-item-avatar>
                            <v-list-item-content>
                                <v-list-item-title v-html="'Photographer: ' + data.item.photographer"></v-list-item-title>
                            </v-list-item-content>
                        </template>
                    </template>
                </v-autocomplete>
            </v-col>
        </v-row>
        <v-row :key="2">
            <v-col :key="1">
                <v-img ref="imgPreview" max-height="67vh" :src.sync="desktopUrl"></v-img>
            </v-col>
        </v-row>
        <v-row :key="3">
            <v-col :key="1" style="text-align: right;">
                <v-btn class="mr-4" @click.prevent="setDefaultDesktopUrl">Default</v-btn>
                <v-btn class="mr-4" @click.prevent="changeDesktopUrl">Save</v-btn>
                <!--<v-btn @click="clear">clear</v-btn>-->
            </v-col>
        </v-row>
        <!--<v-row :key="4">
            <v-col :key="1">
                {{ desktopUrl }}
            </v-col>
        </v-row>-->
    </div>
</template>

<script>
    export default {
        data: () => ({
            desktopUrl: '',
            isUpdating: true,
            wallpapers: null,
            rules: [
                value => !!value || 'Required.',
                value => (value && value.length >= 7) || 'Min 7 characters',
                value => {
                    //eslint-disable-next-line
                    const pattern = /^https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)$/
                    return pattern.test(value) || 'Invalid url.'
                },
            ],
        }),
        mounted() {
            if (localStorage.getItem("desktopUrl") === null) {
                this.setDefaultDesktopUrl();
                //// eslint-disable-next-line
                //console.log('defaultDesktopUrl');
            }
            else {
                this.desktopUrl = localStorage.desktopUrl;
            }
            const baseURI = 'https://api.pexels.com/v1/search?query=wallpaper&per_page=15&page=1'
            this.$http.get(baseURI, { headers: { Authorization: '563492ad6f91700001000001c452b4d2c6af4068b2623ec6df32fcc3' } })
                .then(response => {
                    this.wallpapers = response.data.photos;
                    this.isUpdating = false;
                });
        },
        methods: {
            changeDesktopUrl() {
                this.$store.dispatch('changeDesktopUrlAction', this.desktopUrl);
            },
            defaultDesktopUrl() {
                this.$store.dispatch('setDefaultDesktopUrlAction');
            },
            setDefaultDesktopUrl() {
                this.defaultDesktopUrl();
                this.desktopUrl = this.$store.getters.desktopUrl;
            },
            //getImgUrl() {
            //    if (this.desktopUrl == '') {
            //        return require('@/assets/wallpaper.jpg');
            //    }
            //    else {
            //        return this.desktopUrl;
            //    }
            //  }
        },
        watch: {
            desktopUrl(newDesktopUrl) {
                localStorage.desktopUrl = newDesktopUrl;
                //this.$refs.imgPreview.src = this.getImgUrl();
            }
        }
    }
</script>

<style>
    .v-autocomplete__content.v-menu__content{
        overflow-y: scroll!important;
    }
</style>