<template>
    <transition name="fade" @after-leave="$emit('close')" @after-enter="$emit('open')">
        <div v-show="isOpen" class="window" :style="styleWindow" ref="window" @mousedown="activate" @touchstart="activate">
            <div class="titlebar" :style="styleTitlebar" ref="titlebar">
                <div class="window_title">
                    <v-icon class="window_icon" :color="iconcolor" v-if="icon">{{icon}}</v-icon>
                    <template v-if="$slots.title">
                        <slot name="title" />
                    </template>
                    <template v-else>
                        {{title}}
                    </template>
                </div>
                <template v-if="minimizeButton">
                    <my-button class="bar-button" @click="minimizeButtonClick">&#9866;</my-button>
                </template>
                <template v-if="maximizeButton">
                    <template v-if="isMaximized">
                        <my-button class="bar-button" @click="restoreButtonClick">&#10064;</my-button>
                    </template>
                    <template v-else>
                        <my-button class="bar-button" @click="maximizeButtonClick">&#9744;</my-button>
                    </template>
                </template>
                    <template v-if="closeButton">
                        <my-button class="bar-button" @click="closeButtonClick">&#10005;</my-button>
                    </template>
            </div>
            <div class="content" :style="styleContent" ref="content">
                <slot />
            </div>
        </div>
    </transition>
</template>

<script>
    import { WindowType } from "./script";
    export default WindowType;
</script>

<style lang="scss" scoped>
    .window {
        display: flex;
        flex-flow: column;
        position: absolute;
        border-radius: 4pt 4pt 0 0;
    }

    .titlebar {
        display: flex;
        flex-flow: row nowrap;
        border-radius: 4pt 4pt 0 0;
        font-family: sans-serif;
        /*padding: 0px;*/
        flex: 0 0 auto;
    }

    .window_title {
        flex-grow: 1;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .window_icon{
        margin-right:4pt;
    }
    .content {
        flex-grow: 1;
        padding: 0px;
    }

    .draggable-handle {
        cursor: move;
    }
    .bar-button {
        cursor: default;
        margin-left: 10px;
    }
    .fade-enter,
    .fade-leave-to {
        opacity: 0;
        transform: scale(0.9);
    }

    .fade-enter-active,
    .fade-leave-active {
        transition: 0.2s;
    }
</style>
