<script>
    import MetroTile from './MetroTile.vue'
    import EdgeIcon from '../assets/edge.svg';
    import OneNoteIcon from '../assets/oneNote.svg';
    import PowerPointIcon from '../assets/powerPoint.svg';
    import WeatherIcon from '../assets/weather.svg';
    //import SettingsIcon from '../assets/settings.svg';
    import PhotoIcon from '../assets/photo.svg';
    import ExcelIcon from '../assets/excel.svg';
    import CameraIcon from '../assets/camera.svg';
    import TuneinIcon from '../assets/tunein.svg';
    import YouTubeIcon from '../assets/youtube.svg';
    //import CNNIcon from '../assets/cnn.svg';
    //import StoreIcon from '../assets/store.svg';

    //import { globalMixin } from './mixins/Core.js'

    //const BASE_WIDTH = this.isMobile ? 5 : 3.5;
    //const BASE_HEIGHT = this.isMobile ? 10: 7;
    const MARGIN = 0.375;

    export default {
        name: 'app',
        //mixins: [globalMixin],
        components: {
            MetroTile,
            // eslint-disable-next-line
            EdgeIcon,
            OneNoteIcon,
            PowerPointIcon,
            WeatherIcon,
            //SettingsIcon,
            PhotoIcon,
            ExcelIcon,
            CameraIcon,
            //StoreIcon
            TuneinIcon,
            YouTubeIcon,
            //CNNIcon
        },
        data: () => ({
            //BASE_WIDTH,
            //BASE_HEIGHT,
            rotateNews: 0,
            rotateApps: 0,
            weekDay: (() => {
                const weekDayMap = {
                    0: 'Sunday',
                    1: 'Monday',
                    2: 'Tuesday',
                    3: 'Wednesday',
                    4: 'Thursday',
                    5: 'Friday',
                    6: 'Saturday'
                }
                return weekDayMap[new Date().getDay()];
            })(),
            monthDay: (() => {
                return new Date().getDate();
            })(),
            rotateIndex1: 0,
            rotateArray1: [-90, 90, 90, -90],
            rotateIndex2: 0,
            rotateArray2: [-90, 90],
            miniDrawer: true,
        }),
        methods: { 
            getWidth(ratio, marginRatio = 1.0) {
                var baseWidth  = this.isMobile ? 5 : 3.5;
                return baseWidth * ratio - MARGIN * marginRatio;
            },
            getHeight(ratio, marginRatio = 1.0) {
                var baseHeight = this.isMobile ? 10 : 7;
                return baseHeight * ratio - MARGIN * marginRatio;
            },
            setRotateX(rotateItemName, rotateIndexName, rotateArray) {
                this[rotateItemName] += rotateArray[this[rotateIndexName] % rotateArray.length];
                this[rotateIndexName] += 1;
                //console.log("end setRotateX - " + rotateItemName + "  " + this[rotateItemName] + "  " +  this[rotateIndexName])
            },
            shutdown() {
                this.$store.dispatch('shutdownAction');
                this.intervalShutdown = setInterval(
                () => {
                        this.$store.dispatch('shutdownEndAction');
                }, 5000);
            },
            shutdownEnd() {
                this.$store.dispatch('shutdownEndAction')
            },
            toggleApp(id, isOpen) {
                //if (id == 23) {
                //    this.miniDrawer = true;
                //    console.log("miniDrawer = true");
                //}
                this.$store.dispatch('toggleAppAction', { id, isOpen })
            },
            clickApp() {
                //console.log("clickApp - " + this.rotateIndex2 + "  " + this.rotateApps)
                if (this.rotateApps == 0) {
                    this.toggleApp(22, true);
                }
                else {
                    this.toggleApp(21, true);
                }
            },
            clickNews() {
                //console.log("clickNews - " + this.rotateIndex1 + "  " + this.rotateNews)
                if (this.rotateNews == 0) {
                    this.toggleAppUrl(17, 'https://news.sky.com/world', true);
                    
                }
                else if (this.rotateNews == -90) {
                    this.toggleAppUrl(17, 'https://lite.cnn.com/', true);
                    //this.toggleAppUrl(17, 'https://www.foxnews.com/', true);
                }
                else {
                    this.toggleAppUrl(17, 'https://www.thesun.co.uk/news/', true);
                }
            },
            toggleAppUrl(id, url, isOpen) {
                //console.log("toggleAppUrl - " + url)
                this.$store.dispatch('toggleAppUrlAction', { id, url, isOpen })
            },
            redirect() {
              window.open('https://www.rferl.org', '_blank');
              //this.$root.toggleApp(4, true);
            },
            closeWindow() {
                window.close();
            },
            say: function (message) {
                alert(message);
            }
        },
        computed: {
            apps() {
                return this.$store.getters.apps
            },
            themePrimaryColor() {
                return this.$store.getters.themePrimaryColor
            },
            isMobile() {
                return Boolean(this.$route.query && this.$route.query.mobile)
            }
        },
        mounted() {
            this.interval1 = setInterval(
                () => {
                    this.setRotateX('rotateNews', 'rotateIndex1', this.rotateArray1);
                }, 3000);
            this.interval2 = setInterval(
                () => {
                    this.setRotateX('rotateApps', 'rotateIndex2', this.rotateArray2);
                }, 5000);
        },
        beforeDestroy() {
            clearInterval(this.interval1);
            clearInterval(this.interval2);
            clearInterval(this.intervalShutdown);
        }
    }
</script>

