<template>
    <div style="padding: 10px;">
        <v-form ref="frmApp" v-model="valid">
            <v-row :key="1">
                <v-col :key="1">
                    <v-text-field v-model="app.url"
                                  :counter="100"
                                  :rules="urlRules"
                                  label="Url"
                                  hint="Enter a valid url and click navigate"
                                  persistent-hint
                                  light
                                  append-icon="mdi-chevron-right"
                                  v-on:keyup="navigateKey"
                                  @click:append="navigate"
                                  @change="urlChanged"
                                  required></v-text-field>
                </v-col>
            </v-row>
            <v-row :key="2">
                <v-col :key="1">
                    <v-text-field v-model="app.name"
                                  :counter="30"
                                  :rules="nameRules"
                                  label="Title"
                                  light
                                  required></v-text-field>
                </v-col>
            </v-row>
            <v-row :key="3">
                <v-col :key="1">
                    <IFrameBypass ref="iframeInstall" @iframeloaded="setIsValidUrl(true)" @iframeerror="setIsValidUrl(false)"></IFrameBypass>
                </v-col>
            </v-row>
            <v-row :key="4">
                <v-col :key="1">
                    <v-alert :value="alertValidUrl"
                             type="error">>
                        Please enter a valid URL.
                    </v-alert>
                    <v-alert :value="alertSuccess"
                             type="success">
                        App was installed.
                    </v-alert>
                </v-col>
            </v-row>
            <v-row :key="5">
                <v-col :key="1" style="text-align: right;">
                    <v-btn class="mr-4" 
                           :disabled='!isValidUrl'
                           @click="installApp">Install</v-btn>
                    <!--<v-btn @click="clear">clear</v-btn>-->
                </v-col>
            </v-row>
            <!--<v-row :key="5">
        <v-col :key="1">
            {{ app.url }}
        </v-col>
    </v-row>-->
        </v-form>
    </div>
</template>

<script>
    import IFrameBypass from '../IFrameBypass';

    export default {
        components: {
            IFrameBypass
        },
        data: () => ({
            app: null,
            valid: false,
            isValidUrl: false,
            alertValidUrl: false,
            alertSuccess: false,
            lastUrl: '',
            urlRules: [
                value => !!value || 'Required.',
                value => (value && value.length >= 7) || 'Min 7 characters',
                value => {
                    //eslint-disable-next-line
                    const pattern = /^https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)$/
                    return pattern.test(value) || 'Invalid url.'
                },
            ],
            nameRules: [
                value => !!value || 'Required.',
                value => (value && value.length >= 2) || 'Min 2 characters',
            ],
        }),
        created() {
            this.app = { name: '', url: '', icon: 'mdi-application' };
        },
        mounted() {

        },
        methods: {
            async installApp() {
                this.$refs.frmApp.validate();

                if (this.valid) {
                    if (this.isValidUrl) {
                        await this.$store.dispatch('saveInstalledApp', this.app);
                        this.alertSuccess = true;
                        this.$store.dispatch('getInstalledApps');
                    } else {
                        this.alertValidUrl = true;
                    }
                }
            },
            setIsValidUrl(val) {
                console.log('Install - setIsValidUrl: ' + val) // eslint-disable-line
                this.isValidUrl = val;
            },
            navigateKey(e) {
                if (e.keyCode === 13) {
                    this.navigate();
                }
            },
            urlChanged() {
                if (this.lastUrl != this.app.url) {
                    console.log('Install - urlChanged') // eslint-disable-line
                    this.isValidUrl = false;
                    this.alertSuccess = false;
                }
            },
            navigate() {
                // eslint-disable-next-line
                console.log("Navigates " + this.app.url);
                this.lastUrl = this.app.url;
                this.$refs.iframeInstall.url = this.app.url;
            }
        },
        watch: {
            //eslint-disable-next-line
            //appUrl(newAppUrl) {

            //}
        }
    }
</script>

<style>
    .v-text-field__slot label.v-label {
        left: 5px !important;
    }
</style>