<template>
    <div style="padding:0;margin:0;height:100%;overflow-y:scroll;">
        <!--<v-navigation-drawer
                         light
                         permanent
                         width="15vw">-->
        <v-row :key="1" no-gutters style="width:100%;height:100%;">
            <v-col :key="1" cols="3">
                <v-list light
                        dense
                        width="100%"
                        height="100%">
                    <v-list-item v-for="item in items"
                                 v-bind:key="item.name"
                                 link
                                 v-on:click="currentItem = item">
                        <v-list-item-action>
                            <v-icon color="#3886C4">{{ item.icon }}</v-icon>
                        </v-list-item-action>
                        <v-list-item-content>
                            <v-list-item-title>{{ item.name }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-col>
            <v-col :key="2" cols="9">
                <v-content v-bind:is="currentItem.path" style="width: 99%;"></v-content>
            </v-col>
        </v-row>
</div>
</template>

<script>
    import Home from './Settings/Home.vue';
    import Wallpaper from './Settings/Wallpaper.vue';
    import Color from './Settings/Color.vue';
    import Install from './Settings/Install.vue';
    import Uninstall from './Settings/Uninstall.vue';
    import About from './Settings/About.vue';

      var items = [
          {
              name: "Home",
              icon: "mdi-home-outline",
              path: "Home",
          },
          {
              name: "Wallpaper",
              icon: "mdi-cog",
              path: "Wallpaper",
          },
          {
              name: "Colors",
              icon: "mdi-palette-outline",
              path: "Color",
          },
          {
              name: "Install",
              icon: "mdi-playlist-plus",
              path: "Install",
          },
          {
              name: "Uninstall",
              icon: "mdi-playlist-remove",
              path: "Uninstall",
          },
          {
              name: "About",
              icon: "mdi-information-outline",
              path: "About",
          },
    ];

    export default {
        components: {
            Home,
            Wallpaper,
            Color,
            Install,
            Uninstall,
            About
        },
        data() {
            return {
                items: items,
                currentItem: items[0]
            }
        },
        methods: {
            navigate(index) {
                this.currentItem = items[index];
            }
        },
    };

</script>

