const DB_NAME = 'silveosdb';
const DB_VERSION = 1;
let DB;

export default {

	async getDb() {
		return new Promise((resolve, reject) => {

			if (DB) { return resolve(DB); }
			// eslint-disable-next-line
			console.log('OPENING DB');
			let request = window.indexedDB.open(DB_NAME, DB_VERSION);

			request.onerror = e => {
				// eslint-disable-next-line
				console.log('Error opening db', e);
				reject('Error');
			};

			request.onsuccess = e => {
				DB = e.target.result;
				resolve(DB);
			};

			request.onupgradeneeded = e => {
				// eslint-disable-next-line
				console.log('onupgradeneeded');
				let db = e.target.result;
				db.createObjectStore("apps", { autoIncrement: true, keyPath: 'id' });
			};
		});
	},
	async deleteApp(app) {

		let db = await this.getDb();

		return new Promise(resolve => {

			let trans = db.transaction(['apps'], 'readwrite');
			trans.oncomplete = () => {
				resolve();
			};

			let store = trans.objectStore('apps');
			store.delete(app.id);
		});
	},
	async getApps() {

		let db = await this.getDb();

		return new Promise(resolve => {

			let trans = db.transaction(['apps'], 'readonly');
			trans.oncomplete = () => {
				resolve(apps);
			};

			let store = trans.objectStore('apps');
			let apps = [];

			store.openCursor().onsuccess = e => {
				let cursor = e.target.result;
				if (cursor) {
					apps.push(cursor.value)
					cursor.continue();
				}
			};

		});
	},

	async saveApp(app) {

		let db = await this.getDb();

		return new Promise(resolve => {

			let trans = db.transaction(['apps'], 'readwrite');
			trans.oncomplete = () => {
				resolve();
			};

			let store = trans.objectStore('apps');
			store.put(app);

		});

	}

}
