<template>
    <div :style="customStyle" class="dt">{{ displayTime }}<br />{{ displayDate }}</div>
</template>

<script>
    import moment from 'moment';
    export default {
        props: {
            isMobile: Boolean,
        },
        data() {
            return {
                time: Date.now(),
                selectedFontSize: this.isMobile ? '60%' : '80%'
            }
        },
        computed: {
            displayTime() {
                return moment(this.time).format("HH:mm");
            },
            displayDate() {
                return moment(this.time).format('L');
            },
            customStyle() {
                return {
                    '--sel-font-size': this.selectedFontSize
                }
            }
        },
        created() {
            setInterval(() => {
                this.time = Date.now();
            }, 1000);
        }
    }
</script>

<style scoped>
    .dt {
        font-size: var(--sel-font-size);
        align-content: center;
        text-align: center;
    }
</style>