<template>
    <v-container fluid>
        <v-row v-for="(app,index) in installedApps"
               :key="app.id"
               justify="space-between"
               :class="index % 2 == 0 ? 'grey lighten-5' : 'grey darken-2'"
               no-gutters>
            <v-col key="1">
                <v-card :class="index % 2 == 0 ? 'pa-2 grey--text text--darken-4' : 'pa-2 white--text'"
                        color="rgb(255, 0, 0, 0)"
                        outlined
                        tile>
                    {{ app.name }}
                </v-card>
            </v-col>
            <v-col key="2">
                <v-card :class="index % 2 == 0 ? 'pa-2 grey--text text--darken-4' : 'pa-2 white--text'"
                        color="rgb(255, 0, 0, 0)"
                        outlined
                        tile>
                    {{ app.url }}
                </v-card>
            </v-col>
            <v-col key="3" md="1">
                <v-btn fab text small @click="deleteInstalledApp(app)">
                    <v-icon :class="index % 2 == 0 ? 'grey--text text--darken-4' : 'white--text'"
                            small>mdi-delete-forever</v-icon>
                </v-btn>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    export default {
        data: () => ({
            //apps: null,
        }),
        computed: {
            installedApps() {
                return this.$store.state.installedApps;
            }
        },
        created() {
            this.$store.dispatch('getInstalledApps');
        },
        methods: {
            async deleteInstalledApp(app) {
                console.log('delete', app.id); // eslint-disable-line
                await this.$store.dispatch('deleteInstalledApp', app);
                this.$store.dispatch('getInstalledApps');
            },
        },
    };
</script>

