<template>
    <!--<div>-->
        <iframe id="ifMain"
                ref="ifMain"
                :srcdoc="content"
                src=""
                style="width: 100%;height: 100%;"
                @onload="oniframeload"
                @onerror="oniframeerror"
                sandbox="allow-forms allow-modals allow-pointer-lock allow-popups allow-popups-to-escape-sandbox allow-presentation allow-same-origin allow-scripts allow-top-navigation-by-user-activation"></iframe>
    <!--</div>-->
</template>

<script>
    export default {
        name: 'iframe-bypass',
        props: {
            url: String,
        },
        data: () => ({
            content: String,
            contentTest: String,
            timer: null
        }),
        methods: {
            onload() {
                // eslint-disable-next-line
                console.log("IFrameBypass - onload: the iframe is loaded");
            },
            oniframeload() {
                // eslint-disable-next-line
                console.log("IFrameBypass - oniframeload");
            },
            oniframeerror() {
                // eslint-disable-next-line
                console.log("IFrameBypass - oniframeerror: error on iframe loading");
                this.$emit('iframeerror', event);
            },
            checkIframeLoaded() {
                var iframe = this.$refs.ifMain;
                var iframeDoc = iframe.contentDocument || iframe.contentWindow.document;
                // Check if loading is complete
                if (iframeDoc.readyState == 'complete' || iframeDoc.readyState == 'interactive') {
                    if (iframeDoc.body.innerHTML === "") {
                        // eslint-disable-next-line
                        console.log("IFrameBypass - checkIframeLoaded: iframe body.innerHTML is empty");
                    } else {
                        // eslint-disable-next-line
                        console.log("IFrameBypass - checkIframeLoaded: iframe was loaded");
                        //console.log("iframe was loaded");
                        this.$emit('iframeloaded', event);
                        clearInterval(this.timer);
                        return;
                    }
                }
            },
            load: function (url, options) {
                if (!url) {
                    // eslint-disable-next-line
                    console.log(`IFrameBypass src is empty`);
                    this.content = `<html>
                        <head></head>
                        <body>
                        </body>
                        </html>`;
                    return;
                }
                else if (!url.startsWith('http')) {
                    //throw new Error(`IFrameBypass src ` + this.url + ` does not start with http(s)://`)
                    // eslint-disable-next-line
                    console.log(`IFrameBypass - load: src ` + this.url + ` does not start with http(s)://`);
                    this.content = `<html>
                        <head></head>
                        <body>
                            <div>This url doesn't support SilveOS installing.</div>
                        </body>
                        </html>`;
                    return;
                }

                //this.console.log('IFrameBypass loading:', url)
                this.content = `<html>
                        <head>
                            <style>
                            .loader {
                                position: absolute;
                                top: calc(50% - 25px);
                                left: calc(50% - 25px);
                                width: 50px;
                                height: 50px;
                                background-color: #333;
                                border-radius: 50%;
                                animation: loader 1s infinite ease-in-out;
                            }
                            @keyframes loader {
                                0% {
                                transform: scale(0);
                                }
                                100% {
                                transform: scale(1);
                                opacity: 0;
                                }
                            }
                            </style>
                        </head>
                        <body>
                            <div class="loader"></div>
                        </body>
                        </html>`;

                this.fetchProxy(url, options, 0).then(res => res.text()).then(data => {
                    if (data) {
                        //<link href="/app.js" rel="preload" as="script">
                        this.timer = setInterval(this.checkIframeLoaded, 4000);
                        //calls load method
                        /* eslint-disable */
                        this.content = data.replace(/<head([^>]*)>/i, `<head$1>
                            <base href="${this.url}">
                            <script>
                            // X-Frame-Bypass navigation event handlers
                            document.addEventListener('click', e => {
                                if (window.frameElement && document.activeElement && document.activeElement.href) {
                                    e.preventDefault()
                                    load(document.activeElement.href)
                                }
                            })
                            document.addEventListener('submit', e => {
                                if (window.frameElement && document.activeElement && document.activeElement.form && document.activeElement.form.action) {
                                    e.preventDefault()
                                    if (document.activeElement.form.method === 'post')
                                        load(document.activeElement.form.action, {method: 'post', body: new FormData(document.activeElement.form)})
                                    else
                                        load(document.activeElement.form.action + '?' + new URLSearchParams(new FormData(document.activeElement.form)))
                                }
                            })
                            ${ this.fetchProxy}
                            ${ this.load}
                        <\/script>`)
                        /*eslint-enable */
                        //this.$emit('iframeloaded', event);
                        console.log('IFrameBypass - load: iframe was loaded') // eslint-disable-line
                    }
                    else {
                        console.log('IFrameBypass - load: iframe data is empty') // eslint-disable-line
                    }
                }).catch(e => console.error('IFrameBypass - load: cannot load iframe', e)) // eslint-disable-line
            },

            fetchProxy: function(url, options, i) {
                const proxy = [
                    'https://cors.io/?',
                    'https://jsonp.afeld.me/?url=',
                    'https://cors-anywhere.herokuapp.com/'
                ]
                return fetch(proxy[i] + url, options).then(res => {
                    if (!res.ok)
                        throw new Error(`${res.status} ${res.statusText}`);
                    return res
                }).catch(error => {
                    if (i === proxy.length - 1)
                        throw error
                    return this.fetchProxy(url, options, i + 1)
                })
            }

        },
        created() {
            console.log('IFrameBypass - created') // eslint-disable-line
            this.content = `<html>
                        <head></head>
                        <body>
                        </body>
                        </html>`;
        },
        mounted() {
            //frameDoc = this.$refs.ifMain.contentDocument || this.$refs.ifMain.contentWindow.document;
            //console.log('mounted - load') // eslint-disable-line
            //this.load(this.url, { mode: 'cors' })
        },
        watch: {
            url(newUrl) {
                if (newUrl) {
                    console.log('IFrameBypass - watch: ' + newUrl) // eslint-disable-line
                    this.load(newUrl, { mode: 'cors' })
                }
            }
        }
};
</script>

<style scoped>
</style>
