<template>
    <div id="calculator" ref="calc">

        <div class="calculator-logs">
            <span v-for="log in logs" v-bind:key="log">{{ log }}</span>
        </div>

        <input type="string" class="calculator-input" v-model="value" @keyup.enter="getResult()">

        <div class="calculator-row">
            <div class="calculator-col">
                <button class="calculator-btn gray action" @click="clear()">C</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn gray action" @click="del()">del</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn gray action" @click="addExpresion('%')">%</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn clcaccent action" @click="addExpresion('/')">/</button>
            </div>
        </div>
        <div class="calculator-row">
            <div class="calculator-col">
                <button class="calculator-btn" @click="addExpresion(7)">7</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn" @click="addExpresion(8)">8</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn" @click="addExpresion(9)">9</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn clcaccent action" @click="addExpresion('*')">*</button>
            </div>
        </div>
        <div class="calculator-row">
            <div class="calculator-col">
                <button class="calculator-btn" @click="addExpresion(4)">4</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn" @click="addExpresion(5)">5</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn" @click="addExpresion(6)">6</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn clcaccent action" @click="addExpresion('-')">-</button>
            </div>
        </div>
        <div class="calculator-row">
            <div class="calculator-col">
                <button class="calculator-btn" @click="addExpresion(1)">1</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn" @click="addExpresion(2)">2</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn" @click="addExpresion(3)">3</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn clcaccent action" @click="addExpresion('+')">+</button>
            </div>
        </div>
        <div class="calculator-row">
            <div class="calculator-col wide">
                <button class="calculator-btn" @click="addExpresion(0)">0</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn action" @click="addExpresion('.')">.</button>
            </div>
            <div class="calculator-col">
                <button class="calculator-btn clcaccent action" @click="getResult()">=</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                value: 0,
                logs: []
            }
        },

        methods: {
            addExpresion: function (e) {
                if (Number.isInteger(this.value))
                    this.value = '';
                this.value += e;
            },
            getResult: function () {
                var log = this.value;
                this.value = eval(this.value);
                this.logs.push(log + ("=" + this.value));
            },
            clear: function () {
                this.value = 0;
            },
            del: function () {
                this.value = this.value.slice(0, -1);
            }
        }
    };
</script>

<style lang="scss" scoped>
$darker: #2f2f31;
$dark: #424345;
$gray: #616163;
$white: #fff;
$light: #D4D4D2;
$accent: #f49e3f;

*, ::after, ::before {
  box-sizing: border-box;
}


//  CALCULATOR  \\
#calculator {
  width: 100%;
  margin: 0 auto;
  display: flex;
  padding: 0;
  flex-direction: column;
  background-color: $darker;

  .calculator-logs {
    height: 8.8vh;
    display: flex;
    position: relative;
    overflow: hidden;
    align-items: flex-end;
    flex-direction: column;
    justify-content: flex-end;
    &:before {
      top: 0;
      left: 0;
      right: 0;
      height: 5.3vh;
      content: '';
      z-index: 5;
      position: absolute;      
      background: linear-gradient(to bottom, $darker, rgba($darker, 0));
    }
    span {
      color: $light;
      opacity: .75;
      display: block;
      font-size: 1.5vh;
      text-align: right;
      margin-top: 0.75vh;
      line-height: 1;
      font-weight: lighter;
    }
  }

  .calculator-input {
    color: $light;
    width: 100%;
    border: none;
    padding:  1.3vh 0.8vw;
    display: block;
    font-size: 4.2vh;
    background: none;
    text-align: right;
    font-weight: lighter;
    &:focus, &:active {
      outline: none;
    }
  }

  .calculator-row {
    display: flex;
    padding: 0;
    justify-content: space-around;
    .calculator-col {
      flex: 1;
      box-shadow: 0 0 0 0.05vw $darker;
      &.wide { flex: 2; }
    }
  }

  .calculator-btn {
    width: 100%;
    color: $light;
    border: none;
    cursor: pointer;
    padding: 1.3vh 0.8vw;
    outline: none;
    font-size: 2.8vh;
    transition: all .3s ease-in-out;
    font-weight: 200;
    justify-content: center;
    background-color: $gray;
    &.clcaccent { background-color: $accent; color: $white; }
    &.gray { background-color: $dark; }
    &.action { }
    &:active {
      background-color: $darker;
    }
  }
    /*@media screen and (max-width: 720px) {
        .calculator-btn-size {
            padding: 1.3vh 0.8vw;
            font-size: 2.8vh;
        }
    }*/
}
</style>